/* ===============MAIN/CONTAINER============= */
* {
	box-sizing: border-box;
	color: rgb(142, 6, 0);
}

.black-text {
	color: black;
}

.background {
	background-image:
		linear-gradient(
			rgba(232, 210, 148, 0.3), rgba(232, 210, 148, 0.8)),
			url('./components/bg-texture.jpg');
	background-size: cover;
	box-shadow: inset 0 0 3em rgb(130, 101, 43);
}

main {
	height: 140vh;
	display:grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto auto;
  grid-template-areas:
    "header"
    "search"
		"list"
		"info";
}

.container {
	width: 100vw;
	height: 100%;
}

/* ===============NAV============= */
.navbar-brand {
	color: 1px solid rgb(142, 6, 0)!important;
	margin: 0 auto!important;
}

.navbar-toggler-icon {
	border: none!important;
}

.title {
	color: rgb(142, 6, 0)!important;
	font-size: 1.2em;
	font-weight: lighter;
	margin: 0.7em auto;
	text-align: center;
}

.pages-nav {
	display: flex;
	justify-content: space-evenly;
	text-align: center;
}

.nav-h4 {
	color: rgb(142, 6, 0)!important;
	text-decoration: underline!important;
}

.nav-h4:hover {
	color: rgb(82, 3, 0)!important;
	text-decoration: underline!important;
}

.navbar {
	padding: 1em auto!important;
}

/* ===============MONSTER LIST============= */
.monsterContainer {
	text-align: center;
}

form {
	text-align: center;
}

input{
	background-color: rgba(237, 219, 157, 0.5);
	padding: 0;
}

.search-box {
	grid-area: search;
}

.search {
	border: none;
	border-top: 1px solid rgb(142, 6, 0);
	border-bottom: 1px solid rgb(142, 6, 0);
	font-size: 1.2em;
	line-height: 3em;
	margin: 0.1em;
	text-align: center;
	width: 80%;
}

.search::placeholder {
	font-size: 1.2em;
	color: rgb(142, 6, 0);
	padding: 0 0.4em
}

.monsterList {
	border-right: 1px solid rgb(142, 6, 0);
	border-left: 1px solid rgb(142, 6, 0);
	grid-area: list;
	max-height: 30vh;
	overflow: scroll;
	list-style: none;
	margin-top: 1em;
	padding: 0.2em;
	text-align: center;
}

.monster-button:hover {
  box-shadow: 0 2px 3px 0 rgba(69, 51, 2,0.24), 0 5px 7px 0 rgba(69, 51, 2,0.19);
}

.monster-button:active {
  transform: translateY(1px);
}

.monster-button {
	border: none;
	border-top: 1px solid rgb(142, 6, 0);
	font-size: 1.2em;
	line-height: 2em;
	margin: 0;
	padding: .5em;
	text-align: center;
	width: 80%;
	background-color: rgba(227, 202, 145, 0.2);
}

/* ===============INFO STYLING============= */
.info-container {
	background-color: rgba(237, 219, 157, 0.2);
	grid-area: info;
	margin: 0 auto;
	overflow-y: scroll;
	width: 90%;
	max-height: 75vh;
	padding: 1em;
}

.stats {
	display: flex;
}

.indiv-stat {
	margin: 0 1em;
}

.str {
	margin: 0 1em 0 0;
}

/* DIVIDERS */
.divider {
  border-left: 65vw solid rgb(142, 6, 0);
  border-right: 0em solid transparent;
  border-bottom: 0.2em solid transparent;
  border-top: 0.2em solid transparent;
}

.action {
	border-bottom: 1px solid rgb(142, 6, 0);
}

/* ===============ABOUT STYLING============= */
.about {
	background-color: rgba(237, 219, 157, 0.2);
	grid-area: info;
	margin: 0 auto;
	overflow-y: scroll;
	width: 90%;
	max-height: 75vh;
	padding: 1em;
	overflow-y: scroll;
}

.about-header {
	font-size: 2em;
}

.inline-link {
	color: rgb(142, 6, 0)!important;
	text-decoration: underline!important;
}

.inline-link:hover {
	color: rgb(82, 3, 0)!important;
	text-decoration: underline!important;
}

